import React, { useEffect, useState } from "react";
import useWebSocket from "react-use-websocket";

import MainBoard from "../../components/MainBoard";
import Header from "../../components/Header";
import Footer from "../../components/Footer";

import socketEventsCode from "../../assets/data/SocketEventCode";

import TokenStatusContext from "../../context/TokenStatusContext";
import BalanceStatusContext from "../../context/BalanceStatusContext";
import AuthContext from "../../context/AuthContext";
import GameStatusContext from "../../context/GameStatusContext";
import GameHistoryContext from "../../context/HistoryContext";
import GlobalActiveItems from "../../context/GlobalActiveItemsContext";

const GamePanel: React.FC = () => {
  const [tokenStatus, setTokenStatus] = useState({
    selectedToken: [],
    currentToken: 1,
  });
  const [balanceStatus, setBalanceStatus] = useState({
    totalBalance: 5000,
    betBalance: 0,
  });
  const [gameHistory, setGameHistory] = useState<Array<any>>([]);

  const [userId, setUserId] = useState("");
  const [gameStatus, setGameStatus] = useState(
    socketEventsCode.SOCKET_PLACE_YOUR_BETS
  );

  const socketURI = "ws://roulette.gameonthechain.com:5051";
  const { lastMessage, sendMessage } = useWebSocket(socketURI);

  const [globalActiveItems, setGlobalActiveItems] = useState<Array<number>>([]);

  useEffect(() => {
    if (lastMessage?.data) {
      const socketResponseData = JSON.parse(lastMessage?.data);
      if (socketResponseData.history) {
        setGameHistory(socketResponseData.history);
      }
    }
  }, [lastMessage]);

  return (
    <AuthContext.Provider value={{ userId, setUserId }}>
      <GameStatusContext.Provider value={{ gameStatus, setGameStatus }}>
        <GameHistoryContext.Provider value={{ gameHistory, setGameHistory }}>
          <TokenStatusContext.Provider value={{ tokenStatus, setTokenStatus }}>
            <GlobalActiveItems.Provider value={{globalActiveItems, setGlobalActiveItems}}>
              <BalanceStatusContext.Provider
                value={{ balanceStatus, setBalanceStatus }}
              >
                <Header lastMessage={lastMessage} sendMessage={sendMessage} />
                <MainBoard
                  lastMessage={lastMessage}
                  sendMessage={sendMessage}
                />
                <Footer />
              </BalanceStatusContext.Provider>
            </GlobalActiveItems.Provider>
          </TokenStatusContext.Provider>
        </GameHistoryContext.Provider>
      </GameStatusContext.Provider>
    </AuthContext.Provider>
  );
};

export default GamePanel;
