import React from 'react';

import { ReactComponent as PlaceBetIcon } from "../../../../assets/img/bet-limit.svg";
import { ReactComponent as EmptyFavouriteIcon } from "../../../../assets/img/empty-favourite.svg";

const Favourite:React.FC = () => {
  return (
    <div className='w-full p-4'>
      <div className='w-full text-center pt-6'>
        <h1 className='flex gap-4 items-center justify-center w-full py-2 text-lg text-[#9E7512] border-b border-[#DFAE51]/30'>
          <PlaceBetIcon className='h-8' />
          Place Bets and Save
        </h1>
        <p className='text-[#946E11] py-2'>When ready, Place your Bet and then Save it!</p>
      </div>
      <div className='flex justify-center'>
        <EmptyFavouriteIcon className='w-[350px]' />
      </div>
    </div>
  )
}

export default Favourite